@import-normalize;
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.flex {
    width: auto;
    height: auto;
    margin: 0 auto;
    padding: 10px;
    position: relative;
    align-self: center;
}


.nav {
  background-color:#0a0918;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
  margin:0;
  padding:0;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  
  max-width: 900px;
  padding: 0rem 1rem;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 1rem;
  color: white;
}

.nav-item:hover {
  color: white;
  cursor: pointer;
}
.nav-items{
  padding:0;
  margin:0;
}

.section-dark {
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.home {
	background: linear-gradient(-45deg, #450847, #130827, #1b7fa3, #0c0669);
	background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
            animation: gradient 15s ease infinite;
	height:100vh;
	width: 100vw;
    margin-top: 0;
    padding: 0;
	background-position-y: top;
	position: fixe;
}
.container{
	width:55%;
	height:50%;
	top:40%;
    margin: 0 auto;
	position: relative;
	float: center;
}
.text {
	font-family: sans-serif;
	font-size: 4vw;
	color: azure;
	height: -webkit-max-content;
	height: -moz-max-content;
	height: max-content;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	margin:auto;
	text-align: center;
}
#name{
	color: #FFB81C;
}
.myButton {
	
	background-color:transparent;
	border-radius:3px;
	border:2px solid #FFB81C;
	display:inline-block;
	cursor:pointer;
	color:#FFB81C;
	font-family:sans-serif;
	font-size:18px;
	padding:10px 24px;
	text-decoration:none;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
}
.myButton:hover {
	background-color:#ffffff;
}
.myButton:active {
	position:relative;
	top:1px;
}



@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}



@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.about{
    background-color: rgb(235, 238, 240);
    height:100%;
	width: 100%;
    margin-top: 0;
    padding: 0;
}
.header{
    font-size: 30pt;
	top:20px;
    margin: auto;
	position: relative;
    float: center;
    text-align: center;
    color: #0a0918;
}
.flex{
    justify-content: space-between;
    display:flex;
    flex-flow: row wrap;
    top:10%;
    margin:auto
}
.flex-cols{
    justify-content: flex-start;
    display:flex;
    flex-flow: column wrap;
    top:10%;
    margin:auto;
    padding: 20px;
}
img{
    vertical-align: top;
    margin:auto;
}
.description{
    text-align: center;
    width:250px;
}
#self_pic{
    margin:auto;
}
#header2{
    text-align: center;
    padding-top: 5%;
    padding-bottom: 7%;
    font-size: 20pt;
}
