
.nav {
  background-color:#0a0918;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
  margin:0;
  padding:0;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  
  max-width: 900px;
  padding: 0rem 1rem;
  margin: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 1rem;
  color: white;
}

.nav-item:hover {
  color: white;
  cursor: pointer;
}
.nav-items{
  padding:0;
  margin:0;
}

.section-dark {
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}
